.encabezado{
    position: relative;
    z-index: 30;
}
/* Menu Azul */
.contactoUltraRapido{
    display: flex;
    font-size: 12px;
    font-family: "poppins-regular";
    margin-right: 10%;
}
.contactoUltraRapido p{
    margin-top: 1em;
}
.menuAzul{
    display: flex;
    align-items: center;
    justify-content: center;
}
.redesSocialesMenuAzul-Uno{
    justify-content: center;
    align-items: center;
    margin: 8%;
}
.simboloRedesMenuAzul{
    display: flex;
    margin: 5%;
    margin-top: 11%;
    width: auto;
    height: 40%;
}
.simboloRedesMenuAzulMail{
    margin: 5%;
    margin-top: 7%;
    width: auto;
    height: 40%;
}
.mail-phone{
    margin-left: 16%;
}
.redesSocialesMenuAzul{
    justify-content: flex-end;
    margin-left: 40%;
    align-items: center;
}
/* Menu Blanco */
.Logo{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.ham{
    display: none;
    border: none;
    background: none;
}
.ham span{
    display: block;
    background-color: #FBFBFB;
    height: 3px;
    width: 28px;
    margin: 10px auto;
    border-radius: 2px;
}
#menuham{
    display: flex;
    font-family: "poppins-medium";
    padding: 0;
}
.Logo{
    display: flex;
    text-decoration: none;
    color: #1B22A1;
}
.Logo img{
    width: 24%;
    margin: 3%;
}
.TituloNombreEmpresa{
    font-size: 20px;
    font-family: "poppins-bold";
    margin-left: 1%;
}
/* MENU BLANCO NAV */
.encabezado nav{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.encabezado nav ul{
    list-style: none;
    display: flex;
    justify-content: space-between;
}
li{
    cursor: pointer;
    color: #1B22A1;
    padding: 12px 30px;
    font-family: "poppins-medium";
    display: block;
}
.encabezado nav ul li:hover{
    background-color: #1B22A1;
    color: #FBFBFB;
}
a, Link{
    text-decoration: none;
}
ul{
    padding-inline-start: 0px;
    margin-bottom: 0;
}
.encabezado nav ul li ul{
    display: flex;
    flex-direction: column;
    position: absolute;
}
.encabezado nav ul li ul li{
    left: -33%;
    font-size: 14px;
    background-color: #FBFBFB;
    position: relative;
}
.encabezado nav ul li ul li ul{
    position: absolute;
}
.encabezado nav ul li ul{
    display: none;
}
.encabezado nav ul li:hover > ul{
    display: block;
}
/* RESPONSIVE */
@media (max-width: 768px){
    .menuAzul{
        display: none;
    }
    body{
        overflow: hidden;
    }
    .ham{
        display: block;
        position: absolute;
        top: 10px;
    }
    nav{
        flex-direction: column;
        padding: 25px 0;
    }
    .enlaces-menu{
        padding-top: 5%;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        width: 100%;
        display: none;
        padding: 0;
    }
    .submenu{
        display: none;
        flex-direction: column;
        padding-left: 48%;
        background-color: #262A6D;
        align-items: flex-start;
        list-style-type: none;
        transition: all 0.3s;
    }
    #menuham{
        font-family: "poppins-medium";
        list-style-type: none;
        background-color: #1B22A1;
        padding: 0 40px;
        width: 100%;
        text-align: center;
        padding: 5%;
    }
    #menuham li{
        padding: 2%;
        color: #FBFBFB;
        padding: 3%;
    }
    #menuham a{
        text-decoration: none;
        color: #FBFBFB;
        padding: 3%;
    }
}